import { Component, OnInit } from '@angular/core';
import { ToastController, PopoverController, AlertController } from '@ionic/angular';
import { UserService } from 'src/app/services/user.service';
import { CommonService } from 'src/app/services/common.service';
import { Platform } from '@ionic/angular';
import { environment } from 'src/environments/environment';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.page.html',
  styleUrls: ['./notifications.page.scss'],
})
export class NotificationsPage implements OnInit {
  userInfo: any;
  isLoading: boolean = false;
  notifications: any = [];
  url = environment.url;

  constructor(
    public toastController: ToastController,
    public route: ActivatedRoute,
    public router: Router,
    public plt: Platform,
    public commonService: CommonService,
    public userService: UserService,
    public popoverController: PopoverController,
    public alertController: AlertController,
  ) { }

  ngOnInit() {
    this.userInfo = this.userService.getUserInfo();
  }

  ionViewWillEnter() {
    this.getNotifications();
  }
  readNoti(item: any) {
    item.read_at = 'yes';
    this.commonService.presentToastWithOptions('Notification marked as read.');
  }
  
  viewNoti(item: any) {
    if (item.case_id) {
      this.router.navigate([`/tabs/tab1/case/details/${item.case_id}/active`]);
    } else {
      this.router.navigate([`/tabs/tab2/messages/conversation/${item.room_id}`]);
    }
  }

  getNotifications() {
    this.isLoading = true;
    this.commonService.getNotifications({id: this.userInfo.id})
    .subscribe((data: any) => {
      this.isLoading = false;
      console.log(data, 'ddd');
      this.notifications = data;
    });
  }
}
